.main{
  overflow: hidden;
}

.shops-slider,
.main-slider{
  .swiper{
    &-pagination{
      min-height: 1.875em;
      display: flex;
      align-items: center;
      justify-content: center;
      &-bullet{
        width: 0.375em;
        height: 0.375em;
        background-color: $black;
        margin: 0 .9em !important;
        opacity: 1;
        border: 2px solid $black;
        position: relative;
        &:before{
          content: '';
          @include centerAbsolute;
          width: .625em;
          height: .625em;
          background-color: $black;
          border-radius: 50%;
          transition: inherit;
          opacity: 0;
        }
        &-active{
          background-color: transparent;
          width: 1.875em;
          height: 1.875em;
          &:before{
            opacity: 1;
          }
        }
      }
    }
  }
}

.main-slider{
  position: relative;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      margin: 0;
      top: unset;
      bottom: 0;
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
    &-pagination{
      bottom: 2.19em;

    }
  }
  &__img{
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.logistic{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .875em));
  gap: 1.75em;
  padding: 3.125em 0;
  position: relative;
  &__map{
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    padding: 5em 8em;
  }
  &__text{
    max-width: 32.75em;
    @include grid100gap(3.125em);
  }
  &__bg{
    position: absolute;
    right: 0;
    bottom: 3.125em;
    width: 11.4em;
  }
}

.about{
  padding: 7.3125em 0 2.25em;
  position: relative;
  &__top{
    padding: 0 1.94em;
  }
  &__logo{
    display: block;
    width: 62.5625em;
    max-width: 100%;
    margin-top: 1em;
  }
  &__img{
    display: block;
    float: right;
    width: 30em;
    margin: 0 3.7em;
  }
  &__bg{
    position: absolute;
    left: 0;
    top: 14.95em;
    width: 10.625em;
    transform: rotate(-154.55deg);
  }
  &__bg2{
    position: absolute;
    bottom: 10.81em;
    left: 0;
    width: 5.875em;
  }
}

.about-info{
  padding: 0 18.2em;
  margin-top: -5.5em;
  position: relative;
  z-index: 2;
  &__text{
    max-width: 50.3125em;
    @include grid100gap(2.56em);
    .text16{
      transform: translateX(8.33em);
    }
  }
}

.about-aesthetic{
  width: 57.5625em;
  margin: 4em 0 0 18.2em;
  &__text{
    margin-top: .3125em;
    @include grid100gap(3.25em);
    padding-left: 8.33em;
  }
}

.about-top{
  &__title{
    padding: 0 1.25em;
  }
  &__line{
    max-width: 57.5625em;
    width: 100%;
    height: 0.0625em;
    margin-top: .3125em;
    background-color: $black;
    position: relative;
    img{
      position: absolute;
      right: 100%;
      bottom: 0;
      margin-right: -.625em;
      width: 4.75em;
    }
  }
}

.about-products{
  margin-top: 6.25em;
  padding-left: 18.2em;
  width: 100%;
  &__badge{
    padding: .25em .81em;
    display: flex;
    align-items: center;
    min-height: 2.9375em;
    width: calc(100% - 9em);
    margin-left: auto;
    color: #fff;
    background-color: $primary;
    font-weight: 900;
    span{
      font-size: 1.5em;
      line-height: 160%;
    }
  }
}

.partnership{
  padding: 3.44em 2.62em 3.125em;
  position: relative;
  &__grid{
    padding: 0 7em;
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 3.125em;
    width: 29.6875em;
  }
  @media screen and (min-width: 993px){
    .partnership-card{
      &:nth-child(2){
        padding-right: 18.5em;
      }
      &:nth-child(4){
        padding-right: 12em;
      }
    }
  }
}

.products{
  padding: 2.25em 0 3.44em;
  &__top{
    padding: 0 2.6em 2.3em;
  }
  &__items{
    @include grid100gap(3.125em);
  }
}

.contacts{
  padding: 2.74em 0 0;
  display: grid;
  grid-template-columns: repeat(2,50%);
  position: relative;
  &__img{
    position: absolute;
    left: 0;
    bottom: -7.875em;
    width: 12em;
  }
}

.contacts-info{
  padding: 0 2em;
  &__top{
    @include grid100gap(.6em);
  }
  &__logo{
    display: block;
    max-width: 100%;
    width: 55.2365em;
  }
  &__items{
    margin-top: 3.69em;
    padding-left: 16em;
    @include grid100gap(1.875em);
  }
}

.contact-item{
  display: flex;
  align-items: center;
  font-family: $font2;
  &__icon{
    width: 3.5em;
    height: 3.5em;
    background: #FBEBEB;
    border-radius: 50%;
    @include center;
    margin-right: 2.19em;
    flex-shrink: 0;
    img{
      max-width: 1.625em;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.3125em);
  }
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 118%;
    font-weight: 500;
  }
  &__title{
    text-transform: uppercase;
    color: $primary;
  }
}

.qr{
  margin-top: 2.31em;
  padding-left: 20.8em;
  display: flex;
  align-items: center;
  font-weight: 500;
  font-family: $font2;
  color: #898989;
  img{
    width: 9.375em;
    flex-shrink: 0;
    margin-right: 1.06em;
  }
  span{
    font-size: 1em;
    line-height: 119%;
  }
}

.contacts-map{
  position: relative;
}

.manager{
  position: absolute;
  padding: 2.12em 2.25em;
  background-color: $primary;
  color: #fff;
  width: 30.25em;
  max-width: 100%;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  font-family: $font2;
  &__avatar{
    width: 6.25em;
    height: 6.25em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      border-radius: 50%;
    }
  }
  &__info{
    @include grid100gap(.4em);
  }
  .text18{
    line-height: 122%;
  }
}

.map{
  position: relative;
  height: 48em;
  min-height: 100%;
}

.shops{
  padding: 3.44em 2.6em;
}

.shops-slider{
  padding: 0 7em;
  margin-top: 3.75em;
  .swiper{
    &-container{
      overflow: hidden;
      border-bottom: 1px solid #DBDBDB;
      border-left: 1px solid #DBDBDB;
    }
    &-slide{
      width: 20% ;
      border-top: 1px solid #DBDBDB;
      border-right: 1px solid #DBDBDB;
    }
    &-pagination{
      position: static;
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding-bottom: 2.625em;
  }

  .main-slider{
    .swiper-pagination{
      bottom: unset;
      top: 100%;
      margin-top: .75em;
    }
  }

  .logistic{
    padding: 1.625em 0 1em;
    grid-template-columns: 100%;
    gap: 0;
    &__content{
      padding: 0 1.25em .625em;
      order: -1;
    }
    &__text{
      gap: 1.25em;
    }
    &__bg{
      display: none;
    }
  }

  .about{
    padding: 5.12em 1.25em .6em;
    display: flex;
    flex-direction: column;
    &__top{
      padding: 0;
    }
    &__logo{
      margin-top: .75em;
    }
    &__img{
      order: 10;
      float: unset;
      width: 11.375em;
      margin: 1.63em auto 0 3.7em;
    }
    &__bg{
      width: 5.6875em;
      top: 37.2em;
    }
    &__bg2{
      width: 7.5625em;
      bottom: 26.5em;
      left: -1em;
    }
    @media screen and (min-width: 577px){
      &__bg{
        top: 25em;
      }
      &__bg2{
        bottom: 24em;
      }
    }
  }

  .about-info{
    padding: 0;
    margin-top: -1.03em;
    &__text{
      gap: 1em;
    }
    .text16{
      transform: unset;
    }
  }

  .about-aesthetic{
    margin-left: 0;
    margin-top: 6.37em;
    width: 100%;
    &__text{
      padding: 0;
      gap: 1.2em;
    }
    &__subtitle{
      padding-left: 7.65em;
    }
  }

  .about-top{
    margin-left: 6.4em;
    &__title{
      .text36{
        font-size: 1.875em;
      }
    }
  }

  .about-products{
    padding-left: 0;
    margin-top: 2em;
    position: relative;
    z-index: 2;
    &__badge{
      width: calc(100% - .87em);
      margin-right: -1.25em;
      padding: .81em;
      span{
        font-size: 1.125em;
        line-height: 120%;
      }
    }
  }

  .partnership{
    padding: 1.56em 1.25em;
    &__grid{
      padding: 0;
      margin-top: 1.25em;
    }
    .partnership-card{
      &:nth-child(1){
        .partnership-card__motto{
          padding-top: 2.06em;
        }
      }
    }
    @media screen and (min-width: 577px){
      &__img{
        display: none;
      }
    }
  }

  .products{
    padding: .6em 0 1.6em;
    &__top{
      padding: 0 1.25em 1.13em;
    }
  }

  .contacts{
    grid-template-columns: 100%;
    padding-top: 1em;
    gap: 1.56em;
    &__img{
      display: none;
    }
  }

  .contacts-info{
    padding: 0 1.25em;
    &__logo{
      display: none;
    }
    &__items{
      margin-top: 2.25em;
      padding: 0;
      gap: .94em;
    }
  }

  .contact-item{
    align-items: flex-start;
    &__icon{
      width: 2.5em;
      height: 2.5em;
      margin-right: .94em;
      img{
        max-width: 1.25em;
      }
    }
  }

  .qr{
    margin-top: 1.25em;
    padding: 0;
    img{
      width: 7.5em;
      margin-right: .25em;
    }
    span{
      font-size: .875em;
    }
  }

  .map{
    height: 22em;
    min-height: unset;
  }

  .manager{
    position: static;
    padding: 1.61em 1.25em 1em;
    align-items: flex-start;
    width: 100%;
    &__avatar{
      width: 5.625em;
      height: 5.625em;
    }
    &__info{
      gap: .12em;
    }
    .text15{
      margin-top: .2em;
    }
  }

  .shops{
    padding: 1.56em 1.44em;
  }

  .shops-slider{
    padding: 0;
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 576px){
  .main-slider{
    .swiper{
      &-button{
        display: none;
      }
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .logistic{
    &__map{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .text40{
      font-size: 1.375em;
    }
  }

  .partnership{
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      width: unset;
      max-height: 14em;
      max-width: 80%;
      right: 4.8em;
      bottom: 1.56em;
    }
    .partnership-card:last-child{
      padding-bottom: 14em;
    }
  }
}
