.text40{
  font-size: 2.5em;
  line-height: 125%;
}

.text36{
  font-size: 2.25em;
  line-height: 125%;
}

.text20{
  font-size: 1.25em;
  line-height: 125%;
}

.text18{
  font-size: 1.125em;
  line-height: 160%;
}

.text16{
  font-size: 1em;
  line-height: 160%;
}

.text15{
  font-size: .9375em;
  line-height: 125%;
}

.text14{
  font-size: .875em;
  line-height: 125%;
}

.subtitle{
  font-size: 1em;
  line-height: 125%;
  font-weight: 700;
  letter-spacing: 0.5em;
  text-transform: uppercase;
  color: $gray;
}

.bold-text{
  font-weight: 700;
}

.primary-text{
  color: $primary;
}


@media screen and (max-width: 992px){
  .text40{
    font-size: 1.5em;
  }

  .text36{
    font-size: 1.25em;
  }

  .text20{
    font-size: 1em;
  }
}
