.partnership-card{
  padding: 3.44em 3.13em 1.75em;
  min-height: 16.9375em;
  display: flex;
  align-items: flex-start;
  border: 1px solid #E0E0E0;
  position: relative;
  &:first-child{
    border-top-left-radius: 3.125em;
  }
  &:last-child{
    border-bottom-right-radius: 3.125em;
  }
  &._red{
    background-color: $primary;
    color: #fff;
    border: none;
    .partnership-card__motto{
      color: rgba(255, 255, 255, 0.30);
    }
  }
  &._black{
    background-color: $black;
    color: #fff;
    border: none;
    .partnership-card__motto{
      color: rgba(255, 255, 255, 0.10);
    }
  }
  &__icon{
    width: 6.875em;
    height: 6.875em;
    flex-shrink: 0;
    margin-right: 2.62em;
    display: flex;
    justify-content: center;
    position: relative;
    z-index: 2;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__content{
    @include grid100gap(1.875em);
    max-width: 31em;
  }
  &__title{
    font-size: 1.5em;
    line-height: 125%;
    font-weight: 700;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 160%
  }
  &__motto{
    position: absolute;
    padding: 1em;
    left: 0;
    bottom: 0;
    font-weight: 700;
    font-family: $font2;
    color: rgba(0, 0, 0, 0.10);
    span{
      font-size: 2.25em;
      writing-mode: tb-rl;
      transform: rotate(-180deg) translate(-0px,0px);
    }
  }
}

.product{
  display: grid;
  grid-template-columns: 50% 50%;
  position: relative;
  &__bg{
    position: absolute;
    right: 0;
    width: 12em;
    top: -1em;
  }
  @media screen and (min-width: 577px){
    &:nth-child(even){
      .product{
        &__col{
          &:first-child{
            order: 2;
          }
        }
      }
      .product-slider{
        &__nav,
        &__badge{
          right: unset;
          left: 0;
        }
        .swiper-pagination{
          order: 3;
        }
      }
    }
  }
  &:nth-child(2){
    .product__bg{
      right: unset;
      left: 0;
      top: unset;
      bottom: -.5em;
      width: 8em;
    }
  }
  &:nth-child(3){
    .product__bg{
      top: unset;
      bottom: -8em;
      width: 30.9375em;
    }
  }
  &:nth-child(4){
    .product__bg{
      right: unset;
      left: 0;
      top: unset;
      bottom: -6em;
      width: 13.75em;
    }
  }
  &:nth-child(5){
    .product__bg{
      top: unset;
      bottom: 10.625em;
      width: 11.375em;
    }
  }
}

.product-slider{
  overflow: hidden;
  position: relative;
  &__img{
    @include imgCover(85.1%);
  }
  &__nav{
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 2;
    display: flex;
  }
  &__badge{
    position: absolute;
    right: 0;
    top: 2.5em;
    padding: 0 2.5em;
    height: 4.75em;
    background-color: #fff;
    display: flex;
    align-items: center;
    z-index: 2;
    border-bottom: 1px solid $primary;
    span{
      font-size: 1.25em;
      line-height: 125%;
    }
  }
  .swiper{
    &-button{
      position: static;
      margin: 0;
      flex-shrink: 0;
      align-self: flex-end;
      &-next{
        border-left: 1px solid #D6D6D6;
      }
    }
    &-pagination{
      position: static;
      width: unset;
      padding: 0 1.7em .7em;
      display: flex;
      align-items: flex-end;
      font-weight: 700;
      @include sameNumWidth;
      ._total{
        font-size: 1.125em;
        line-height: 128%;
        padding-bottom: .5em;
        margin-left: .33em;
      }
      ._current{
        font-size: 3em;
        line-height: 125%;
      }
    }
  }
}

.product-info{
  padding: 3.62em 4.13em;
  &__logo{
    display: block;
    width: 11.875em;
  }
  &__title{
    margin-top: 3.31em;
    max-width: 27em;
  }
  &__items{
    margin-top: 3.125em;
    padding: 0 3.19em;
    @include grid100gap(.94em);
  }
}

.product-advantage{
  display: flex;
  align-items: center;
  &__icon{
    width: 3.75em;
    height: 3.75em;
    flex-shrink: 0;
    margin-right: 1.56em;
    @include center;
  }
  img{
    max-width: 100%;
    max-height: 100%;
    display: block;
    flex-shrink: 0;
  }
}

.shop-card{
  position: relative;
  padding-bottom: 100%;
  &__inner{
    position: absolute;
    padding: 1em;
    @include center;
    flex-direction: column;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  &__img{
    height: 11em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__link{
    line-height: 180%;
    text-decoration-line: underline;
    color: $primary;
  }
}


@media screen and (max-width: 992px){
  .partnership-card{
    padding: .94em 1.12em 1.12em 2.75em;
    display: block;
    &__icon{
      width: 3.75em;
      height: 3.75em;
    }
    &__content{
      gap: .625em;
      margin-top: 1em;
    }
    &__title{
      font-size: 1.25em;
    }
    &__desc{
      font-size: 1em;
    }
    &__motto{
      padding: 1.2em 0;
      bottom: unset;
      top: 0;
      span{
        font-size: 1.875em;
        line-height: 120%;
      }
    }
  }

  .product{
    &__bg{
      display: none;
    }
  }

  .product-slider{
    &__badge{
      padding: 0 .9em;
      height: 2.25em;
      top: .625em;
      span{
        font-size: 1em;
      }
    }
    .swiper{
      &-pagination{
        padding-bottom: 0;
        padding-right: 1em;
        ._current{
          font-size: 2.5em;
        }
      }
    }
  }

  .product-info{
    padding: 1.28em 1.25em 0;
    &__logo{
      width: 6.3em;
    }
    &__title{
      margin-top: 1.25em;
      max-width: 16em;
    }
    &__items{
      margin-top: 1.44em;
      padding: 0;
      gap: .75em;
    }
  }

  .product-advantage{
    img{
      width: 2.5em;
      margin-right: .63em;
    }
  }
}


@media screen and (max-width: 576px){
  .product{
    grid-template-columns: 100%;
  }

  .shop-card{
    &__img{
      height: 4.3125em;
      margin-bottom: .5em;
    }
  }
}
