.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(0, 0, 0, 0.60);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        width: 87.8125em;
        max-width: calc(100% - 2.5em);
        position: relative;
        z-index: 3;
        overflow: hidden;
    }
    &__scroll{
        overflow-y: auto;
        overflow-x: hidden;
        max-height: calc(100 * var(--vh));
        padding: 5.75em 8.2em 1em 5.5em;
        background-image: url(../img/modal-bg.png);
        background-repeat: no-repeat;
        background-position: left bottom 2.56em;
        background-size: 42.125em auto;
    }
    &__close {
        position: absolute;
        border-top: 0;
        top: .94em;
        right: .87em;
        background-color: transparent;
        width: 1.875em;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
    &__grid{
        display: grid;
        grid-template-columns: 21em calc(100% - 26em);
        gap: 1.25em 5em;
    }
}

.consult-form{
    padding-bottom: 15em;
    position: relative;
    &__fields{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .5em));
        gap: 1.38em 1em;
        ._full{
            grid-column: 1/-1;
        }
    }
    &__wrap{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 3.125em;
    }
    &__confirm{
        font-size: .75em;
        line-height: 180%;
        color: #797979;
    }
    &__btn{
        position: absolute;
        right: -8.2em;
        bottom: -1em;
        width: 28.8125em;
        height: 10.25em;
        justify-content: unset;
        align-items: unset;
        padding: 2.56em 2.87em;
        font-weight: 700;
        text-transform: uppercase;
        &:hover{
            img{
                transform: translateX(1em);
            }
        }
        span{
            font-size: 1em;
            line-height: 130%;
        }
        img{
            position: absolute;
            width: 8em;
            right: 3.71em;
            bottom: 2.13em;
            transition: all .3s;
        }
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__scroll{
            padding: 1em 1.25em 1em;
            overflow: unset;
            overflow-x: hidden;
            background-position: left bottom 7em;
            background-size: 10em auto;
            background-attachment: fixed;
        }
        &__grid{
            grid-template-columns: 100%;
        }
    }

    .consult-form{
        padding-bottom: 9.1275em;
        &__wrap{
            margin-top: .56em;
        }
        &__btn{
            width: calc(100% + 2.5em);
            height: 7.1875em;
            right: -1.25em;
            padding-top: 1.94em;
            img{
                right: 1.14em;
                bottom: 1.25em;
            }
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__scroll{
            background-size: 100% auto;
        }
        .text40{
            padding-right: 1.5em;
            span{
                display: none;
            }
        }
    }

    .consult-form{
        &__fields{
            grid-template-columns: 100%;
            gap: .56em;
        }
        &__wrap{
            flex-direction: column;
            align-items: flex-start;
            .file-input{
                margin-bottom: 1.25em;
            }
        }
        &__confirm{
            line-height: 120%;
        }
    }
}
