.header{
  padding-left: 1.25em;
  position: relative;
  z-index: 2;
  &:before{
    content: '';
    position: absolute;
    top: 100%;
    right: 0;
    width: calc(100% - 2.875em - 30.3125em);
    height: 1px;
    background: $black;
  }
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    width: 30.3125em;
    margin-right: 1.625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    width: 20.625em;
    height: 7.0625em;
    flex-shrink: 0;
    margin-left: 4.63em;
    img{
      margin-right: 1.25em;
      width: 1.56513em;
      flex-shrink: 0;
    }
  }
}

.burger{
  display: none;
}

.header-contacts{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 0 3.9em;
  &__phone{
    font-size: 1em;
    line-height: 125%;
    font-weight: 500;
    margin-bottom: .12em;
  }
  &__mail{
    font-size: 0.75em;
    line-height: 125%;
    font-weight: 500;
    letter-spacing: 0.3em;
    text-transform: uppercase;
    color: $primary;
  }
}

.menu{
  flex-shrink: 0;
  flex-grow: 1;
  &__top{
    display: none;
  }
}

.nav{
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__link,
  &__show,
  &__sublink{
    font-size: 1.125em;
    line-height: 128%;
    font-weight: 700;
    text-transform: uppercase;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: $primary;
      path{
        fill: $primary;
      }
    }
  }
  &__sublink{
    white-space: nowrap;
    font-size: 1em;
    @include lastM(0 0 1em);
  }
  &__show{
    display: flex;
    align-items: center;
    svg{
      margin-left: 1.25em;
      width: .5em;
      height: auto;
    }
  }
  @media screen and (min-width: 993px){
    &__item{
      position: relative;
      &:hover{
        .nav__hidden{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &__hidden{
      position: absolute;
      top: 99%;
      padding-top: 1em;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      &-inner{
        background-color: #fff;
        padding: 1em;
        box-shadow: 0 0 .9375em rgba(0, 0, 0, .15);
        display: flex;
        flex-direction: column;
        align-items: flex-start;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding-left: 0;
    padding-bottom: .44em;
    &:before{
      display: none;
    }
    &__inner{
      flex-wrap: wrap;
    }
    &__logo{
      width: calc(100% - 5.375em);
      padding: 0 1em;
      margin: 0;
      @include center;
      img{
        width: 12.5em;
        max-width: 100%;
      }
    }
    &__btn{
      width: 2.875em;
      height: 3.5em;
      margin: 0;
      span{
        display: none;
      }
      img{
        margin: 0;
      }
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .burger{
    width: 1.625em;
    display: block;
    margin-left: .875em;
    img{
      width: 100%;
    }
  }

  .header-contacts{
    margin: .5em auto 0;
    padding: 0;
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    background-color: #FFF;
    background-image: url(../img/menu-bg.png);
    background-repeat: no-repeat;
    background-position: left bottom;
    background-size: 17.5em auto;
    z-index: 100;
    width: 100%;
    height: 100vh;
    padding: 1.31em 1em 10em;
    overflow-y: auto;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__top{
      @include center;
      margin-bottom: 5em;
    }
    &__logo{
      display: block;
      width: 9.5em;
    }
    &__close{
      position: absolute;
      top: 1.25em;
      right: 1.25em;
      width: 1.875em;
      img{
        width: 100%;
        display: block;
      }
    }
  }

  .nav{
    height: unset;
    flex-direction: column;
    justify-content: unset;
    &__item,
    &__link{
      @include lastM(0 0 3.125em);
    }
    &__show{
      justify-content: center;
      svg{
        margin-left: .25em;
      }
    }
    &__hidden{
      padding-top: 1.25em;
      display: none;
      &-inner{
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }
  }
}
