.fg {
  position: relative;
  margin: 0;
  textarea,
  input {
    border: 1px solid #C2C2C2;
    padding: 0 1.15em;
    height: 2.9em;
    width: 100%;
    font-size: 1.25em;
    line-height: 125%;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #797979;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    height: 6em;
    resize: none;
    padding-top: .75em;
    padding-bottom: .75em;
  }
}

.file-input{
  display: flex;
  align-items: center;
  color: $primary;
  position: relative;
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
    &:hover{
      &~svg{
        path{
          fill: $primary;
        }
      }
      &~span{
        border-color: transparent;
      }
    }
  }
  svg{
    width: 1.375em;
    height: auto;
    margin-right: 1em;
  }
  span{
    font-size: 1.25em;
    line-height: 150%;
    border-bottom: 1px dashed $primary;
    transition: all .3s;
  }
}
