.footer{
  padding: 4.56em 9.63em 4.88em 18.5em;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item{
    font-size: 1em;
    line-height: 119%;
    color: #898989;
    font-weight: 500;
    font-family: $font2;
  }
  &__by{
    display: inline-flex;
    align-items: center;
    margin-top: auto;
    margin-left: 3em;
    svg{
      width: 2.5em;
      height: auto;
      margin-right: 1em;
    }
    span{
      font-size: .75em;
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.56em 1.25em 2.56em;
    &__item{
      font-size: .875em;
    }
    &__by{
      svg{
        width: 2em;
      }
      span{
        font-size: .75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      flex-direction: column;
    }
    &__item{
      @include lastM(0 0 .7em);
    }
    &__by{
      order: 3;
      margin-top: 1em;
    }
  }
}
