@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
._animate{
  opacity: 0;
  transition: all 1.2s;
  transform: translateY(2em);
  &._opacity-only{
    transform: translateY(0);
  }
  &._animated{
    opacity: 1;
    transform: translateY(0);
  }
}

.swiper-button{
  width: 6.25em;
  height: 6.25em;
  background-color: #fff;
  svg{
    width: 1.3125em;
    height: auto;
  }
  &:hover{
    path{
      fill: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .swiper-button{
    width: 3.125em;
    height: 3.125em;
    svg{
      width: .9375em;
    }
  }
}
